class AstVisualizer {
  constructor() {
    this.nodes = new vis.DataSet([]);
    this.edges = new vis.DataSet([]);
    this.id = 0;
  }

  drawCommands(commands) {
    for (const command of commands) {
      this.drawCommand(command);
    }

    return {
      nodes: this.nodes,
      edges: this.edges
    };
  }

  makeNode(label) {
    this.id++;
    this.nodes.add({
      id: this.id,
      label
    });
    return this.id;
  }

  tableToString(table) {
    if (table.database) {
      return "".concat(table.database, ".").concat(table.name);
    } else {
      return table.name;
    }
  }

  columnToString(column) {
    if (column.table) {
      return "".concat(this.tableToString(column.table), ".").concat(column.name);
    } else {
      return column.name;
    }
  }

  drawCommand(command) {
    switch (command.kind) {
      case "use":
        this.makeNode("USE ".concat(command.database));
        break;

      case "select":
        {
          const columnString = command.columns === "*" ? "*" : command.columns.map(this.columnToString).join(", ");
          const selectNode = this.makeNode("SELECT ".concat(columnString));
          const fromNode = this.makeNode(this.tableToString(command.table));
          this.edges.add({
            from: selectNode,
            to: fromNode,
            label: "FROM"
          });

          if (command.whereCondition) {
            const whereNode = this.drawExpression(command.whereCondition);
            this.edges.add({
              from: selectNode,
              to: whereNode,
              label: "WHERE"
            });
          }

          if (command.orderBy.length > 0) {
            const orderByString = command.orderBy.map(this.columnToString).join(", ");
            const orderByNode = this.makeNode(orderByString);
            this.edges.add({
              from: selectNode,
              to: orderByNode,
              label: "ORDER BY"
            });
          }

          break;
        }

      case "insert":
        {
          const insertNode = this.makeNode("INSERT");
          const columnString = command.columns.map(this.columnToString);
          const intoNode = this.makeNode("".concat(this.tableToString(command.table), "\n").concat(columnString));
          this.edges.add({
            from: insertNode,
            to: intoNode,
            label: "INTO"
          });
          const valuesNode = this.makeNode("VALUES");
          this.edges.add({
            from: insertNode,
            to: valuesNode
          });

          for (const value of command.values) {
            const valueNode = this.makeNode(this.constantToString(value));
            this.edges.add({
              from: valuesNode,
              to: valueNode
            });
          }

          break;
        }

      case "delete":
        {
          const deleteNode = this.makeNode("DELETE");
          const fromNode = this.makeNode(this.tableToString(command.table));
          this.edges.add({
            from: deleteNode,
            to: fromNode,
            label: "FROM"
          });

          if (command.whereCondition) {
            const whereNode = this.drawExpression(command.whereCondition);
            this.edges.add({
              from: deleteNode,
              to: whereNode,
              label: "WHERE"
            });
          }
        }
    }
  }

  constantToString(constant) {
    switch (constant.kind) {
      case "intLiteral":
        return "int\n".concat(constant.value);

      case "stringLiteral":
        return "string\n".concat(constant.value);

      case "null":
        return "NULL";

      case "now":
        return "NOW()";
    }
  }

  drawExpression(expression) {
    switch (expression.kind) {
      case "intLiteral":
      case "stringLiteral":
      case "null":
      case "now":
        return this.makeNode(this.constantToString(expression));

      case "column":
        return this.makeNode(this.columnToString(expression));

      case "unary":
        {
          const operatorNode = this.makeNode("unary\n".concat(expression.operator.toUpperCase()));
          const operandNode = this.drawExpression(expression.operand);
          this.edges.add({
            from: operatorNode,
            to: operandNode
          });
          return operatorNode;
        }

      case "binary":
        {
          const operatorNode = this.makeNode("binary\n".concat(expression.operator.toUpperCase()));
          const leftOperandNode = this.drawExpression(expression.leftOperand);
          const rightOperandNode = this.drawExpression(expression.rightOperand);
          this.edges.add({
            from: operatorNode,
            to: leftOperandNode
          });
          this.edges.add({
            from: operatorNode,
            to: rightOperandNode
          });
          return operatorNode;
        }
    }
  }

}